
canvas {
  position: absolute;
  left: 400px;
  top: 0;
  opacity: 0;
  transition: left 200ms, top 200ms, opacity 300ms ease-in-out;
}
body {
  margin: 0;
  padding: 0;
  overflow: hidden;
  position: fixed;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  transition: background 200ms;
}
